<template>
    <v-container>

        <!-- Ficha de una entrega -->
        <v-card color="blue" v-if="entregaSeleccionada != null" class="my-3">
            <v-row><v-col><v-btn @click="clickEnVerMapa()" color="green" block dark><div class="itemUnaEntrega">Ver en el mapa</div></v-btn></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega"># {{entregaSeleccionada.OrdenEntrega}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Comprobante}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreCliente}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.NombreDestino}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Domicilio}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">{{entregaSeleccionada.Localidad}}</div></v-col></v-row>
            <v-row><v-col align="center" ><div class="itemUnaEntrega">Bultos: {{entregaSeleccionada.Bultos}}</div></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnOK()" color="green" block dark><div class="itemUnaEntrega">OK 👍</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnNo()" color="red" block dark><div class="itemUnaEntrega">NO 👎</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnParcial()" color="orange" block dark><div class="itemUnaEntrega">PARCIAL 🤔</div></v-btn></v-col></v-row>
            <v-row><v-col><v-btn @click="clickEnCancelar()" color="blue" block dark><div class="itemUnaEntrega">CANCELAR 🤦‍♂️</div></v-btn></v-col></v-row>
        </v-card>

        <!-- Lista de entregas pendientes -->
        <v-alert color="green" class="py-0 mt-0 mb-1 itemEntrega" v-if="entregaSeleccionada==null">{{getTituloListaEntregas()}}</v-alert>
        <v-alert color="blue" class="py-0 my-0 itemEntrega" v-if="entregaSeleccionada==null">Entregas pendientes ({{getListaEntregas(true).length}})</v-alert>
        <v-alert @click="clickEnEntrega(item)" :color="getColorEntrega(item)" v-for="(item, index) in getListaEntregas(true)" class="my-1" v-if="entregaSeleccionada==null">
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega"># {{item.OrdenEntrega}} - {{item.NombreCliente}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.Comprobante}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.NombreDestino}}</div>
                </v-col>
            </v-row>
        </v-alert>

        <!-- Lista de entregas ya realizadas -->
        <v-alert color="green" class="py-0 mb-0 mt-5 itemEntrega" v-if="entregaSeleccionada==null">Entregas realizadas ({{getListaEntregas(false).length}})</v-alert>
        <v-alert :color="getColorEntrega(item)" v-for="(item, index) in getListaEntregas(false)" class="my-1" v-if="entregaSeleccionada==null">
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega"># {{item.OrdenEntrega}} - {{item.NombreCliente}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.Comprobante}}</div>
                </v-col>
            </v-row>
            <v-row class="py-0 my-0">
                <v-col align="center" class="py-0 my-0">
                    <div class="itemEntrega">{{item.NombreDestino}}</div>
                </v-col>
            </v-row>
        </v-alert>
    </v-container>
</template>

<style scoped>
    .itemUnaEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
    .itemEntrega {
        color: white;
        font-size: 16px;
        font-weight: 900;
        font-family: "Helvetica Neue";
    }
</style>

<script>
import choferes from "@/store/choferes"
import store from "@/store"
import fechas from "vue-lsi-util/fechas"


export default {
    name: "VerRutaActual",

    data() {
        return {
            listaEntregas: [],
            entregaSeleccionada: null,
        }
    },

    methods: {
        getTituloListaEntregas() {
            return "Entregas para el "+fechas.getHoy()
        },
        getEntregas() {            
            choferes.actions.getEntregas(store.state.choferes.choferActual.numero, fechas.getHoy(), 1) 
                .then(respuesta => {
                   this.listaEntregas=respuesta
                   this.listaEntregas.sort((a, b) => {
                       if (a.OrdenEntrega>b.OrdenEntrega) {
                           return 1
                       } else {
                           return -1
                       }
                   })
                })
                .catch(puteada => {
                    console.log(puteada)
                })
        },
        clickEnVerMapa() {
            window.open("http://maps.google.com/maps?q="+this.entregaSeleccionada.Latitud+","+this.entregaSeleccionada.Longitud, "_blank").focus()
        },
        getListaEntregas(pendiente) {
            if (pendiente) {
                return this.listaEntregas.filter(e=>e.Estado=='RECOLECTADO')
            } else {
                return this.listaEntregas.filter(e=>e.Estado!='RECOLECTADO')
            }
        },
        getColorEntrega(item) {
            if (item.Estado=="ENTREGADO") {
                return "green"
            } else if (item.Estado=="NO ENTREGADO") {
                return "red"
            } else if (item.Estado=="PARCIAL") {
                return "orange"
            } else {  
                return "blue"
            }
        },
        clickEnCancelar() {
            this.entregaSeleccionada=null
        },
        clickEnOK() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="ENTREGADO"
            this.entregaSeleccionada=null
        },
        clickEnNo() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="NO ENTREGADO"
            this.entregaSeleccionada=null
        },
        clickEnParcial() {
            const ubicacionEntrega=this.listaEntregas.indexOf(this.entregaSeleccionada)
            this.listaEntregas[ubicacionEntrega].Estado="PARCIAL"
            this.entregaSeleccionada=null
        },
        clickEnEntrega(item) {
            this.entregaSeleccionada=item
        },
    },

    created() {
        this.getEntregas()
    }
}
</script>