import { differenceInCalendarDays } from 'date-fns'

const fechas = {
    getHoy() {
        const hoy=new Date()
        const fecha=String(hoy.getFullYear())+"-"+String(hoy.getMonth()+1).padStart(2, "0")+"-"+String(hoy.getDate()).padStart(2, "0")
        return fecha
    },

    getDistanciaEntreFechas(fecha1, fecha2) {
        const fechaDiferencia1=new Date(fecha1.substr(0, 4), parseInt(fecha1.substr(5, 2))-1, fecha1.substr(8, 2))
        const fechaDiferencia2=new Date(fecha2.substr(0, 4), parseInt(fecha2.substr(5, 2))-1, fecha2.substr(8, 2))
        return differenceInCalendarDays(fechaDiferencia1, fechaDiferencia2)                    
    }    

}

export default fechas